body {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #eee;
}

.list-container {
  width: 90vw;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.list-container > * {
  margin: 0 1rem;
}

@media( max-width: 760px ) {
  .list-container {
    flex-direction: column;
  }

  .list-container > * {
    margin: 1rem 0;
  }
}
